        ></v-text-field>
      </v-col>
    </v-row> -->
    <v-data-table
      :headers="headers"
      :items="records"
      item-key="id"
      class="elevation-1"
      :search="search"
      :loading="isLoading"
      loading-text="Loading... Please wait"
      :sort-by.sync="sortBy"
      :sort-desc.sync="sortDesc"
    >
      <template v-slot:item.actions="{ item }">
        <v-tooltip right>
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              v-bind="attrs"
              v-on="on"
              @click="viewInfo(item)"
              icon
              small
            >
              <v-icon
                size="large"
                color="blue darken-3"
              >
                mdi-note-edit-outline
              </v-icon>
            </v-btn>
          </template>
          <span>View / Update</span>
        </v-tooltip>
      </template>

      <template v-slot:item.is_active="{ item }">
        <v-chip
          :color="item.is_active=='Active' ? 'success' : 'red'"
          dark
          small
          label
        >
          <v-icon 
            v-if="item.is_active=='Active'" 
            left
            size="medium"
          >
            mdi-check
          </v-icon>
          <v-icon 
            v-else 
            left
            size="medium"
          >
            mdi-close
          </v-icon>
          {{ item.is_active }}
        </v-chip>
      </template>
    </v-data-table>
    <ViewInfoModal v-if="viewInfoModal" :modalData="viewInfoModalData" @closeModal="viewInfoModal = false" @refresh="handleFilter" />
    <AddModal v-if="addModal" :modalData="addModalData" @closeModal="addModal = false" @refresh="handleFilter" />
  </v-container>
</template>

<script>
import { mapState, mapActions } from "vuex"
import AddModal from "@/components/questions/AddModal"
import ViewInfoModal from "@/components/questions/ViewInfoModal"

export default {
  components: { AddModal, ViewInfoModal },
  data: () => ({
    search: '',
    sortBy: 'created_at',
    sortDesc: true,
    headers: [
      { 
        text: 'Action', 
        value: 'actions', 
        sortable: false,
        align: 'center'
      },
      { text: 'Status', value: 'is_active' },
      { text: 'Question', value: 'question' },
      { text: 'Choices Count', value: 'choices_count' },
      { text: 'Added By', value: 'added_by' },
      { text: 'Created At', value: 'created_at' },
      { text: 'Updated At', value: 'updated_at' },
    ],
    viewInfoModal: false,
    viewInfoModalData: [],
    addModal: false,
    addModalData: [],
  }),
  created() {
    this.handleFilter()
  },
  computed: {
    ...mapState("questions", ["records", "isLoading"]),
    /* tableData() {
      return this.records.map(record => {
        return { ...record, parseChoices: JSON.parse(record.choices), choicesCount: JSON.parse(record.choices).length }
      })
    } */
  },
  methods: {
    ...mapActions("questions", ["getQuestions"]),
    handleFilter() {
      if (!this.isLoading) {
        this.$store.commit("questions/setRecords", [])
        this.getQuestions()
      }
    },
    viewInfo(item) {
      this.viewInfoModal = true
      this.viewInfoModalData = { ...item, dialog: this.viewInfoModal }
    },
    showAddModal() {
      this.addModal = true
      this.addModalData = { dialog: this.addModal }
    },
  }
}
</script>