<template>
  <v-dialog
    v-model="dialog"
    width="1000"
  >
    <v-card>
      <v-card-title>
        <span class="text-h6">Add Question</span>
      </v-card-title>
      <v-divider></v-divider>
      <v-card-text class="pt-3 pb-6">
        <v-alert
          :value="!!addError"
          dense
          outlined
          type="error"
          transition="scale-transition"
          icon="mdi-exclamation-thick"
          class="mb-3"
        >
          {{ addError }}
        </v-alert>
        <v-form
          ref="form"
          v-model="valid"
          @submit.prevent="handleSubmit"
        >
          <div class="mb-2">
            <small>* indicates required field</small>
          </div>
          <v-row>
            <v-col
              cols="12"
            >
              <v-text-field
                ref="question"
                v-model="question"
                :rules="questionRules"
                label="Question*"
                counter="200"
                maxlength="200"
                hide-details="auto"
              ></v-text-field>
            </v-col>
          </v-row>
          <div class="mt-5">
            <p>Choices</p>
            <v-row v-for="(choice, index) in choices" :key="index">
              <v-col
                cols="12"
                v-if="index > 0"
              >
                <v-divider class="brown lighten-5"></v-divider>
              </v-col>
              <v-col
                cols="12"
                sm="9"
                md="9"
              >
                <v-text-field
                  v-model="choice.description"
                  :rules="descriptionRules"
                  label="Description*"
                  counter="150"
                  maxlength="150"
                  hide-details="auto"
                ></v-text-field>
              </v-col>
              <v-col
                cols="12"
                sm="3"
                md="3"
              >
                <v-select
                  v-model="choice.is_correct"
                  :rules="is_correctRules"
                  :items="isCorrect"
                  label="Is Correct*"
                  hide-details="auto"
                ></v-select>
              </v-col>
              <v-col
                cols="12"
                v-if="index > 1"
              >
                <v-btn
                  color="red darken-1"
                  class="text-capitalize"
                  @click="removeChoice(index)"
                  small
                  outlined
                >
                  <v-icon left>
                    mdi-delete-outline
                  </v-icon>
                  Remove
                </v-btn>
              </v-col>
            </v-row>
            <v-btn
              color="info"
              class="mt-5"
              small
              @click="addChoice"
              outlined
            >
              <v-icon left>
                mdi-plus
              </v-icon>
              Add Choice
            </v-btn>
          </div>
          <div class="mt-5">
            <v-btn
              :disabled="!valid"
              type="submit"
              color="blue darken-3 white--text"
              class="mr-2 mt-2"
              :loading="addLoading"
              depressed
            >
              Add
            </v-btn>
            <v-btn
              @click="closeModal"
              color="secondary"
              class="mt-2"
              depressed
              outlined
            >
              Cancel
            </v-btn>
          </div>
        </v-form>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>
import { mapState, mapActions } from "vuex"

export default {
  props: {
    modalData: {
      type: Object,
      required: true
    }
  },
  data: () => ({
    valid: true,
    question: '',
    questionRules: [
      v => !!v || 'This field is required.'
    ],
    choices: [
      {
        description: '',
        is_correct: 'No'
      },
      /* {
        description: '',
        is_correct: 'No'
      },
      {
        description: '',
        is_correct: 'No'
      },
      {
        description: '',
        is_correct: 'No'
      } */
    ],
    descriptionRules: [
      v => !!v || 'This field is required.'
    ],
    isCorrect: [
      'No',
      'Yes'
    ],
    is_correctRules: [
      v => !!v || 'This field is required.'
    ]
  }),
  computed: {
    ...mapState("questions", ["addLoading", "addError"]),
    dialog: {
      get: function () {
        return this.modalData.dialog
      },
      // setter
      set: function () {
        this.closeModal()
      }
    },
  },
  mounted() {
    this.$refs.question.focus()
  },
  methods: {
    ...mapActions("questions", ["addQuestion"]),
    addChoice() {
      this.choices.push(
        {
          description: '',
          is_correct: 'No'
        }
      )
    },
    removeChoice(index) {
      this.choices.splice(index, 1)
    },
    async handleSubmit () {
      if (!this.addLoading) {
        await this.addQuestion({ question: this.question, choices: this.choices })

        if (!this.addError) {
          // reset form
          this.$refs.form.reset()
          // this.$refs.question.focus()
          this.$emit('refresh')
          // this.dialog = false

          this.closeModal()
        }
      }
    },

    closeModal() { 
      this.$emit('closeModal')
    }
  },
  /* watch: {
    dialog(visible) {
      if (visible) {
        setTimeout(() => {
          this.$refs.question.focus()
        }, 200);
      } else {
        // remove error
        this.$store.commit("questions/setAddError", null)

        // reset validation when form closed
        // this.$refs.form.resetValidation()
      }
    }
  }, */
}
</script>